:root {
  background-color: #5876ad8d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  border-width: 0cm;
  font-family : Verdana, Geneva, Tahoma, sans-serif;
  font-size: medium;
}

p {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 20px;
  color: white;
}

.buttons{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 10%;
  padding-right: 10%;
}
